'use client';

import { createContext, useContext, useState, useEffect, ReactNode, useMemo, Dispatch, SetStateAction } from 'react';
import { ALL_SKILLS } from '@/lib/skills';

type SkillsState = { [key: string]: boolean };

type SkillContextType = {
  skills: SkillsState;
  setSkills: Dispatch<SetStateAction<SkillsState>>;
  toggleSkill: (skill: string) => void;
  isInitialized: boolean;
};

const SkillContext = createContext<SkillContextType | undefined>(undefined);

const SKILLS_STORAGE_KEY = 'web3skills';

const initialSkills = Object.keys(ALL_SKILLS).reduce((acc, skill) => {
    acc[skill] = false;
    return acc;
}, {} as SkillsState);


export function SkillProvider({ children }: { children: ReactNode }) {
  const [skills, setSkills] = useState<SkillsState>({});
  const [isInitialized, setIsInitialized] = useState(false);

  useEffect(() => {
    try {
      const stored = localStorage.getItem(SKILLS_STORAGE_KEY);
      if (stored) {
        const storedSkills = JSON.parse(stored);
        const validSkills: SkillsState = {};
        for (const skillName of Object.keys(ALL_SKILLS)) {
            validSkills[skillName] = !!storedSkills[skillName];
        }
        setSkills(validSkills);
      } else {
        setSkills(initialSkills);
      }
    } catch (error) {
      console.error("Failed to load skills from localStorage", error);
      setSkills(initialSkills);
    }
    setIsInitialized(true);
  }, []);

  useEffect(() => {
    if (isInitialized) {
        try {
            localStorage.setItem(SKILLS_STORAGE_KEY, JSON.stringify(skills));
        } catch (error) {
            console.error("Failed to save skills to localStorage", error);
        }
    }
  }, [skills, isInitialized]);
  
  const toggleSkill = (skill: string) => {
    setSkills(prev => ({ ...prev, [skill]: !prev[skill] }));
  };

  const value = useMemo(() => ({
    skills,
    setSkills,
    toggleSkill,
    isInitialized,
  }), [skills, isInitialized]);

  return (
    <SkillContext.Provider value={value}>
        {children}
    </SkillContext.Provider>
  );
}

export const useSkills = () => {
  const context = useContext(SkillContext);
  if (context === undefined) {
    throw new Error('useSkills must be used within a SkillProvider');
  }
  return context;
};
